// –§—É–Ω–∫—Ü—ñ—ó, —è–∫—ñ –ø–µ—Ä–µ–¥–∞—é—Ç—å—Å—è –∫–æ–ª–±–µ–∫–∞–º–∏ –≤ addEventListners

import {
  products,
  notFound,
  loadMoreBtn,
  searchInput,
  clearSearchBtn,
} from './refs'; // –µ–ª–µ–º–µ–Ω—Ç —Ä–æ–∑–º—ñ—Ç–∫–∏
import {
  getProducts,
  getProductsByCategory,
  getProductById,
  searchProducts,
} from './products-api';

import {
  addToCart,
  removeFromCart,
  isInCart,
  updateCartCounter,
} from './storage';

import { createMarkupProducts, renderModalProduct } from './render-function'; // —Ä–æ–∑–º—ñ—Ç–∫–∞
import { openModal } from './modal';

let currentPage = 1;
let currentCategory;

export async function handleClickCategoryBtn(event) {
  const clickedElement = event.target; // –µ–ª–µ–º–µ–Ω—Ç –Ω–∞ —è–∫–∏–π –∫–ª—ñ–∫–Ω—É–ª–∏
  // —á–∏ –Ω–∞—Ç–∏—Å–Ω—É—Ç–∞ —Å–∞–º–µ –∫–Ω–æ–ø–∫–∞
  if (!event.target.classList.contains('categories__btn')) {
    console.log('not a btn');
    return;
  }

  const category = event.target.textContent;
  currentCategory = category; //  –∑–∞–ø–∏—Å—É—é –∞–∫—Ç–∏–≤–Ω—É –∫–∞—Ç–µ–≥–æ—Ä—ñ—é

  /////////////////////////////              ???
  //  –ø—Ä–∏ –∫–ª—ñ–∫—É –Ω–∞ –æ–¥–Ω—É –∑ –∫–Ω–æ–ø–æ–∫ .categories__btn, —Ç—Ä–µ–±–∞:
  // –ó–Ω—è—Ç–∏ –∫–ª–∞—Å categories__btn--active –∑ —É—Å—ñ—Ö –∫–Ω–æ–ø–æ–∫ (—î —Ç—ñ–ª—å–∫–∏ –æ–¥–∏–Ω –∞–∫—Ç–∏–≤–Ω–∏–π).  –î–æ–¥–∞—Ç–∏ —Ü–µ–π –∫–ª–∞—Å –¥–æ —Ç—ñ—î—ó –∫–Ω–æ–ø–∫–∏, —è–∫—É –Ω–∞—Ç–∏—Å–Ω—É–ª–∏.
  //–û—á–∏—Å—Ç–∏—Ç–∏ –∞–∫—Ç–∏–≤–Ω—ñ –∫–ª–∞—Å–∏ –∑ —É—Å—ñ—Ö –∫–Ω–æ–ø–æ–∫. —à—É–∫–∞—î –ø–æ –í–°–Ü–ô —Å—Ç–æ—Ä—ñ–Ω—Ü—ñ,
  document
    .querySelectorAll('.categories__btn--active')
    .forEach(btn => btn.classList.remove('categories__btn--active'));
  // –î–æ–¥–∞—Ç–∏ –∫–ª–∞—Å –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç—ñ –¥–æ –Ω–æ–≤–æ—ó –∫–Ω–æ–ø–∫–∏
  clickedElement.classList.add('categories__btn--active');
  /////////////////////

  products.innerHTML = ''; // –æ—á–∏—â–∞—î–º–æ —Å–ø–∏—Å–æ–∫, —è–∫—â–æ –≤—ñ–Ω –±—É–≤
  currentPage = 1; //  –ø–æ—á–∞—Ç–æ–∫ –∑ –ø–µ—Ä—à–æ—ó —Å—Ç–æ—Ä—ñ–Ω–∫–∏

  let items = [];
  try {
    if (category === 'All') {
      items = await getProducts(currentPage);
    } else {
      items = await getProductsByCategory(category, currentPage);
    }
    // –Ø–∫—â–æ —Ç–æ–≤–∞—Ä—ñ–≤ –Ω–µ–º–∞—î ‚Äî –ø–æ–∫–∞–∑–∞—Ç–∏ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è
    if (!items.length) {
      notFound.classList.add('not-found--visible');
      loadMoreBtn.classList.add('hidden'); //  —Ö–æ–≤–∞—î–º–æ –∫–Ω–æ–ø–∫—É
      return;
    } else {
      notFound.classList.remove('not-found--visible');
    }
    console.log('btn-category', items);

    createMarkupProducts(items);

    // –ü–æ–∫–∞–∑–∞—Ç–∏ –∫–Ω–æ–ø–∫—É, —è–∫—â–æ —î —â–µ —Ç–æ–≤–∞—Ä–∏ (—Ä—ñ–≤–Ω–æ 12 ‚Äî —ñ–º–æ–≤—ñ—Ä–Ω—ñ—Å—Ç—å, —â–æ —â–µ –±—É–¥—É—Ç—å)

    if (items.length === 12) {
      loadMoreBtn.classList.remove('hidden');
    } else {
      loadMoreBtn.classList.add('hidden');
    }
    //
  } catch (error) {
    console.log(error);
  }

  // getProducnsByCategory(category);
  // –ø—Ä–∏ –∫–ª—ñ–∫—É –≤–∏–≤–æ–¥–∏—Ç—Å—è –Ω–∞–∑–≤–∞ –∫–Ω–æ–ø–∫–∏
  // –ø—Ä–∏ –∫–ª—ñ–∫—É –≤ –æ–±—Ä–∞–Ω—É –∫–∞—Ç–µ–≥–æ—Ä—ñ—é –ø–æ—Ç—Ä—ñ–±–Ω–æ –ø—Ä–æ—á–∏—Ç–∞—Ç–∏ —Ç–µ–∫—Å—Ç–æ–≤–∏–π –∫–æ–Ω—Ç–µ–Ω—Ç –∫–Ω–æ–ø–∫–∏
  // console.log('category', category);
}

//         –§—É–Ω–∫—Ü—ñ—è –æ–±—Ä–æ–±–∫–∏ Load More

export async function handleLoadMore() {
  currentPage += 1;

  try {
    let items = [];

    if (currentCategory === 'All') {
      items = await getProducts(currentPage);
    } else {
      items = await getProductsByCategory(currentCategory, currentPage);
    }

    if (!items.length) {
      loadMoreBtn.classList.add('hidden');
      return;
    }

    createMarkupProducts(items);
    console.log('btn-load-More', items);

    if (items.length < 12) {
      loadMoreBtn.classList.add('hidden');
    }
  } catch (error) {
    console.error(error);
  }
}

//       –∫–ª—ñ–∫ –ø–æ –∫–∞—Ä—Ç—Ü—ñ

export async function handleClickProductCard(event) {
  const productItem = event.target.closest('.products__item');
  if (!productItem) return;

  const productId = productItem.dataset.id;
  if (!productId) return;

  try {
    const product = await getProductById(productId);
    renderModalProduct(product);
    openModal();
  } catch (error) {
    console.error('Error loading product', error);
  }
}

//              –æ–±—Ä–æ–±–∫–∞ —Å–∞–±–º—ñ—Ç—É —Ñ–æ—Ä–º–∏ –ø–æ—à—É–∫—É

export async function handleSearchSubmit(event) {
  event.preventDefault();

  const query = searchInput.value.trim();
  if (!query) return;

  try {
    const result = await searchProducts(query, 1);
    products.innerHTML = '';

    if (!result.length) {
      notFound.classList.add('not-found--visible');
      loadMoreBtn.classList.add('hidden');
    } else {
      createMarkupProducts(result);
      notFound.classList.remove('not-found--visible');
      loadMoreBtn.classList.add('hidden'); //  –Ω–µ –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—î–º–æ load more –¥–ª—è –ø–æ—à—É–∫—É
    }
  } catch (error) {
    console.error('Search error', error);
  }
}

//    –æ–±—Ä–æ–±–∫–∞ –µ–Ω–æ–ø–∫–∏ –æ—á–∏—Å—Ç–∏—Ç–∏ –ø–æ—à—É–∫

export async function handleClearSearch() {
  searchInput.value = '';
  clearSearchBtn.classList.add('hidden');

  try {
    const result = await getProducts(1);
    products.innerHTML = '';

    if (!result.length) {
      notFound.classList.add('not-found--visible');
    } else {
      createMarkupProducts(result);
      notFound.classList.remove('not-found--visible');
      loadMoreBtn.classList.remove('hidden'); //  –∑–Ω–æ–≤—É –ø–æ–∫–∞–∑—É—î–º–æ load more
    }
  } catch (error) {
    console.error('Clear search error', error);
  }
}

//       –û–±—Ä–æ–±–∫–∞ –∫–ª—ñ–∫—É –ø–æ –∫–Ω–æ–ø—Ü—ñ –≤ –º–æ–¥–∞–ª—Ü—ñ

export function handleCartBtnClick(event) {
  const btn = event.target;
  const productId = Number(btn.closest('.modal-product').dataset.id);

  if (!productId) return;

  if (isInCart(productId)) {
    removeFromCart(productId);
    btn.textContent = 'Add to Cart';
  } else {
    addToCart(productId);
    btn.textContent = 'Remove from Cart';
  }

  updateCartCounter(); // –æ–Ω–æ–≤–∏—Ç–∏ –ª—ñ—á–∏–ª—å–Ω–∏–∫ —É —Ö–µ–¥–µ—Ä—ñ
}

//              Wishlist

export function handleWishlistBtnClick(event) {
  const btn = event.target;
  const productId = Number(btn.closest('.modal-product').dataset.id);
  if (!productId) return;

  if (isInWishlist(productId)) {
    removeFromWishlist(productId);
    btn.textContent = 'Add to Wishlist';
  } else {
    addToWishlist(productId);
    btn.textContent = 'Remove from Wishlist';
  }

  updateWishlistCounter(); // –æ–Ω–æ–≤–ª–µ–Ω–Ω—è –ª—ñ—á–∏–ª—å–Ω–∏–∫–∞
}
