//–û–ø–∏—Å–∞–Ω–∞ —Ä–æ–±–æ—Ç–∞ –º–æ–¥–∞–ª–∫–∏ - –≤—ñ–¥–∫—Ä–∏—Ç—Ç—è –∑–∞–∫—Ä–∏—Ç—Ç—è —ñ –≤—Å–µ —â–æ –∑ –º–æ–¥–∞–ª–∫–æ—é –ø–æ–≤ º—è–∑–∞–Ω–æ

import { modal, modalCloseBtn, modalContent } from './refs';

export function openModal() {
  modal.classList.add('modal--is-open');

  // –î–æ–¥–∞—î–º–æ —Å–ª—É—Ö–∞—á—ñ
  document.addEventListener('keydown', handleEscPress);
  modal.addEventListener('click', handleBackdropClick);
  modalCloseBtn.addEventListener('click', closeModal);
}

export function closeModal() {
  modal.classList.remove('modal--is-open');

  // –í–∏–¥–∞–ª—è—î–º–æ —Å–ª—É—Ö–∞—á—ñ
  document.removeEventListener('keydown', handleEscPress);
  modal.removeEventListener('click', handleBackdropClick);
  modalCloseBtn.removeEventListener('click', closeModal);
}

// –û–±—Ä–æ–±–∫–∞ –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—è ESC
function handleEscPress(event) {
  if (event.key === 'Escape') {
    closeModal();
  }
}

// –û–±—Ä–æ–±–∫–∞ –∫–ª—ñ–∫—É –ø–æ –±–µ–∫–¥—Ä–æ–ø—É (–∞–ª–µ –Ω–µ –ø–æ –º–æ–¥–∞–ª—å–Ω–æ–º—É –≤–º—ñ—Å—Ç—É)
function handleBackdropClick(event) {
  if (!modalContent.contains(event.target)) {
    // contains() ‚Äî –¥–æ–∑–≤–æ–ª—è—î –≤—ñ–¥—Ä—ñ–∑–Ω–∏—Ç–∏ –∫–ª—ñ–∫ –ø–æ –∫–æ–Ω—Ç–µ–Ω—Ç—É –≤—ñ–¥ –∫–ª—ñ–∫—É –ø–æ —Ñ–æ–Ω—É.
    closeModal();
  }
}
