//–†–æ–±–æ—Ç–∞ –∑ loacalStorage

const CART_KEY = 'cart';
const WISHLIST_KEY = 'wishlist';

import { wishlistCount, cartCount } from './refs';
//

export function getCartItems() {
  const raw = localStorage.getItem(CART_KEY);
  return raw ? JSON.parse(raw) : [];
}
//  –∑—á–∏—Ç—É—î –∑–±–µ—Ä–µ–∂–µ–Ω—ñ –¥–∞–Ω—ñ –∑ localStorage (—Ç–æ–≤–∞—Ä–∏ –∑ –∫–æ—à–∏–∫–∞) –∑–∞ –∫–ª—é—á–µ–º CART_KEY ('cart'). –Ø–∫—â–æ –¥–∞–Ω—ñ —î, –≤–æ–Ω–∞ –ø–µ—Ä–µ—Ç–≤–æ—Ä—é—î —ó—Ö –∑ JSON-—Ä—è–¥–∫–∞ –Ω–∞ JavaScript-–º–∞—Å–∏–≤ —ñ –ø–æ–≤–µ—Ä—Ç–∞—î –π–æ–≥–æ; –≤ —ñ–Ω—à–æ–º—É –≤–∏–ø–∞–¥–∫—É –ø–æ–≤–µ—Ä—Ç–∞—î –ø–æ—Ä–æ–∂–Ω—ñ–π –º–∞—Å–∏–≤.

export function addToCart(productId) {
  const cart = getCartItems();
  if (!cart.includes(productId)) {
    cart.push(productId);
    localStorage.setItem(CART_KEY, JSON.stringify(cart));
  }
}
//  –¥–æ–¥–∞—î —Ç–æ–≤–∞—Ä –¥–æ –∫–æ—à–∏–∫–∞. —Å–ø–æ—á–∞—Ç–∫—É –æ—Ç—Ä–∏–º—É—î –ø–æ—Ç–æ—á–Ω–∏–π –≤–º—ñ—Å—Ç –∫–æ—à–∏–∫–∞. —è–∫—â–æ —Ç–æ–≤–∞—Ä –∑ –≤–∫–∞–∑–∞–Ω–∏–º productId —â–µ –Ω–µ –∑–Ω–∞—Ö–æ–¥–∏—Ç—å—Å—è –≤ –∫–æ—à–∏–∫—É, –≤–æ–Ω–∞ –¥–æ–¥–∞—î –π–æ–≥–æ —ñ –æ–Ω–æ–≤–ª—é—î –¥–∞–Ω—ñ –≤ localStorage.

export function removeFromCart(productId) {
  const cart = getCartItems().filter(id => id !== productId);
  localStorage.setItem(CART_KEY, JSON.stringify(cart));
}
//  –≤–∏–¥–∞–ª—è—î —Ç–æ–≤–∞—Ä –∑ –∫–æ—à–∏–∫–∞. –æ—Ç—Ä–∏–º—É—î –ø–æ—Ç–æ—á–Ω–∏–π –≤–º—ñ—Å—Ç –∫–æ—à–∏–∫–∞, —Ñ—ñ–ª—å—Ç—Ä—É—î –π–æ–≥–æ, –∑–∞–ª–∏—à–∞—é—á–∏ —Ç—ñ–ª—å–∫–∏ —Ç—ñ —Ç–æ–≤–∞—Ä–∏, id —è–∫–∏—Ö –Ω–µ –¥–æ—Ä—ñ–≤–Ω—é—î productId, —ñ –ø–æ—Ç—ñ–º –æ–Ω–æ–≤–ª—é—î –∫–æ—à–∏–∫ —É localStorage –Ω–æ–≤–∏–º, –≤—ñ–¥—Ñ—ñ–ª—å—Ç—Ä–æ–≤–∞–Ω–∏–º –º–∞—Å–∏–≤–æ–º.

export function isInCart(productId) {
  return getCartItems().includes(productId);
}
//  –ø–µ—Ä–µ–≤—ñ—Ä—è—î, —á–∏ –∑–Ω–∞—Ö–æ–¥–∏—Ç—å—Å—è —Ç–æ–≤–∞—Ä —É –∫–æ—à–∏–∫—É. –æ—Ç—Ä–∏–º—É—î –ø–æ—Ç–æ—á–Ω–∏–π –≤–º—ñ—Å—Ç –∫–æ—à–∏–∫–∞ —ñ –ø–µ—Ä–µ–≤—ñ—Ä—è—î, —á–∏ –º—ñ—Å—Ç–∏—Ç—å –º–∞—Å–∏–≤ productId. –ø–æ–≤–µ—Ä—Ç–∞—î true, —è–∫—â–æ —Ç–æ–≤–∞—Ä —î –≤ –∫–æ—à–∏–∫—É, —ñ false ‚Äî —è–∫—â–æ –Ω–µ–º–∞—î.
//

//  –æ–Ω–æ–≤–ª—é—î–º–æ –∑–Ω–∞—á–µ–Ω–Ω—è
export function updateCartCounter() {
  const cart = getCartItems();
  cartCount.textContent = cart.length;
}

//               Wishlist

//
//  –æ—Ç—Ä–∏–º—É—î–º–æ –º–∞—Å–∏–≤ ID —ñ–∑ localStorage
export function getWishlistItems() {
  const raw = localStorage.getItem(WISHLIST_KEY);
  return raw ? JSON.parse(raw) : [];
}

export function addToWishlist(productId) {
  const list = getWishlistItems();
  if (!list.includes(productId)) {
    list.push(productId);
    localStorage.setItem(WISHLIST_KEY, JSON.stringify(list));
  }
}

export function removeFromWishlist(productId) {
  const list = getWishlistItems().filter(id => id !== productId);
  localStorage.setItem(WISHLIST_KEY, JSON.stringify(list));
}

export function isInWishlist(productId) {
  return getWishlistItems().includes(productId);
}

export function updateWishlistCounter() {
  const list = getWishlistItems();
  wishlistCount.textContent = list.length;
}
